@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@400;700&display=swap");
@import url("./animate.min.css");
@import "variable.scss";

::selection {
  background: $pri;
  color: #fff;
}

::-webkit-scrollbar {
  width: 0.75rem;
  display: none;
}

::-webkit-scrollbar-track {
  background: #fff;
}

::-webkit-scrollbar-thumb {
  background: #333;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: #999;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-decoration: none;
  border: none;
  outline: none;
  vertical-align: baseline;
  cursor: default;
  word-break: keep-all;
}

a {
  cursor: pointer;
  color: #333;
}

input,
button {
  border: none;
  outline: none;
  font-family: "Noto Sans KR", sans-serif;
  cursor: auto;

  &:active {
    border: none;
    outline: none;
  }
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"] {
  -moz-appearance: textfield;
}

input {
  border-bottom: 1px solid #333;
  padding: 0.25rem 0;

  &:focus {
    border-bottom: 1px solid $pri;
  }

  &:active {
    border-bottom: 1px solid $pri;
  }
}

::placeholder {
  color: $sub;
  font-weight: 100;
}

button {
  cursor: pointer;
}

ul,
li {
  list-style: none;
}

html {
  font-family: "Noto Sans KR", -apple-system, sans-serif;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #fff;
  width: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
  color: #333;
}

#app {
  position: relative;
  top: 0;
  left: 0;
}

.main-app {
  position: relative;
}

.content-section {
  position: relative;
  width: 100%;
}

.header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: #fff;
  height: 5rem;
  transition: height 0.3s $easing;
  overflow: hidden;
  z-index: 9999;

  &.active {
    height: 16rem;
  }

  .header-wrap {
    width: 100%;
    height: 5rem;
    max-width: 1280px;
    margin: 0 auto;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding: 0 1.5rem;
    background: #fff;

    .logo {
      width: 8rem;
      height: 5rem;
      top: 0;
      left: 1.5rem;
      position: absolute;
      background: url("../img/logo.svg") center no-repeat;
      background-size: contain;
    }
  }

  .gnb {
    display: flex;
    position: absolute;
    top: 0;
    height: 5rem;
    padding-top: 1.65rem;
  }

  .menu {
    position: relative;

    &:nth-child(2) {
      p {
        width: 9rem;
      }
    }

    &:nth-child(3) {
      p {
        width: 7.5rem;
      }
    }

    &:hover {
      p {
        color: $pri;

        &:after {
          transform: scale(1) rotate(0) translateX(0) translateY(0);
        }
      }
    }

    p {
      font-size: 1rem;
      font-weight: 600;
      transition: color 0.2s;
      width: 9.5rem;
      position: relative;

      &:after {
        content: "";
        display: block;
        width: 0.75rem;
        height: 0.75rem;
        background: url(../img/wing.svg) center no-repeat;
        background-size: contain;
        position: absolute;
        top: -0.4rem;
        left: -0.6rem;
        transition: transform 0.2s $easing;
        transform: scale(0) rotate(45deg) translateX(0.75rem)
          translateY(0.25rem);
      }
    }

    ul {
      position: absolute;
      font-size: 0.9rem;
      width: 100%;
      height: 12rem;
      margin-top: 1.8rem;

      li {
        font-weight: 300;
        width: 100%;

        a {
          width: 100%;
          padding: 0.42rem 0;
          display: inline-block;
        }

        &:hover {
          font-weight: 500;
        }
      }
    }
  }
}

.title {
  font-size: 3rem;
  font-weight: 700;
  letter-spacing: -1px;
  margin-bottom: 1rem;
}

.text {
  font-size: 1.5rem;
  font-weight: 300;
  letter-spacing: -1px;
  line-height: 1.75;
  margin-top: 1rem;
}

strong {
  font-weight: 500;
}

.app-market {
  position: fixed;
  bottom: 4rem;
  right: 4rem;
  display: flex;
  transition: transform 0.9s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transform: translateY(8rem);

  &.active {
    transform: translateY(0rem);
  }

  .market {
    cursor: pointer;
    width: 44px;
    height: 44px;
    border: 1px solid #d1d1d9;
    border-radius: 50%;

    &.apple {
      background-color: rgba(255, 255, 255, 0.25);
      background-image: url("../img/apple.svg");
      background-position: left center;
      background-repeat: no-repeat;
      background-size: cover;
      transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);

      &:hover {
        background-position: right center;
        border-color: $pri;
        background-color: $pri;
      }
    }

    &.google {
      margin-left: 2rem;
      background-color: rgba(255, 255, 255, 0.25);
      background-image: url("../img/google.svg");
      background-position: left center;
      background-repeat: no-repeat;
      background-size: cover;
      transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);

      &:hover {
        background-position: right center;
        border-color: $pri;
        background-color: $pri;
      }
    }
  }
}

.main-section {
  max-width: 1280px;
  margin: 0 auto;
}

.wrapper {
  padding: 0 1.5rem;
  position: relative;
}

.footer {
  width: 100%;
  background: #292626;
  padding: 4rem 0;

  .footer-wrap {
    width: 100%;
    max-width: 1280px;
    padding: 0 1.5rem;
    margin: 0 auto;
    color: #fff;
  }

  .term-box {
    display: flex;
    font-size: 1.1rem;

    p {
      cursor: pointer;
      transition: opacity 0.3s;
      opacity: 0.5;
      font-weight: 400;

      &:hover {
        opacity: 1;
      }

      &:last-child {
        margin-left: 4rem;
      }
    }
  }

  .company-box {
    width: 100%;
    display: flex;
    margin-top: 5rem;
    font-size: 0.85rem;
    align-items: center;

    span {
      display: inline-block;
      font-weight: 300;
      margin: 0 1.5rem;
      opacity: 0.5;
      &.ml {
        margin-left: 3rem;
      }
    }

    .xwidget {
      width: 6rem;
      height: 2rem;
      background: url("../img/xwidget.svg") center no-repeat;
      background-size: contain;
      opacity: 0.5;
    }
  }
}
