@import "variable.scss";

.main-section {
  &.bottom {
    height: 100vh;
    background: $sub;
    margin-top: 300px;
    max-width: initial;
    width: 100%;
  }

  span {
    &.typing {
      display: block;
      font-size: 2rem;
      margin: 0.5rem 0;
      position: absolute;
      left: 0.2rem;

      span {
        color: #333;
        font-weight: 300;
      }

      &.top {
        top: -4.25rem;
      }
    }
  }

  .main-wrapper {
    .main-pay-app {
      top: 1rem;
      width: 100%;
      height: 200vh;
      position: relative;
      z-index: 91;
      right: 1rem;

      .pay01 {
        position: absolute;
        width: 100%;
        height: 100vh;
        background: url("../img/pay01.png") center 60% no-repeat;
        background-size: calc(17vw + 6rem);
        z-index: 99;

        &.reveal {
          background: url("../img/pay01-01.png") center 60% no-repeat;
          background-size: calc(17vw + 6rem);
          z-index: 98;
        }

        &.card {
          background: url("../img/pay01-card.png") center 65% no-repeat;
          background-size: calc(17vw + 6rem);
          z-index: 97;
        }
      }

      .pay02 {
        position: absolute;
        width: 100%;
        height: 100vh;
        background: url("../img/pay03.png") 21% 60% no-repeat;
        background-size: calc(16vw + 4rem);
        z-index: 97;
      }

      .pay03 {
        position: absolute;
        width: 100%;
        height: 100vh;
        background: url("../img/pay02.png") 78% 60% no-repeat;
        background-size: calc(16vw + 4rem);
        z-index: 97;
      }
    }

    .h1-box {
      top: calc(51vh - 4rem);
      z-index: 90;
      position: absolute;
      left: 1.5rem;

      h1 {
        font-family: "raleway";
        font-weight: 600;
        display: inline-block;
        color: $pri;
        font-size: calc(4vw + 1rem);
        line-height: 0.75;
      }

      h2 {
        font-family: "raleway";
        font-weight: 400;
        display: inline-block;
        letter-spacing: -8px;
        color: $pri;
        font-size: calc(4vw + 1rem);
        margin-top: 1rem;
        line-height: 0.75;
      }
    }

    .pay-desc {
      width: 100%;
      max-width: 900px;
      position: absolute;
      left: calc(50% - 450px);
      bottom: -8rem;
    }

    .text {
      width: 70%;
    }
  }
}
