$easing: cubic-bezier(0.7, 0.375, 0.31, 0.99);
$black: #363a3f;
$pri: #ff3c64;
$pri2: #fc5c7c;
$sub: #dbbfbc;
$sub2: #b99b9b;
$red: #f11e6f;

$breakpoint-mobile: 576px;
$breakpoint-tablet: 900px;
$breakpoint-desktop: 1367px;

@mixin mobile {
  @media (max-width: #{$breakpoint-mobile}) {
    @content;
  }
}

@mixin tablet {
  @media (min-width: #{$breakpoint-tablet}) and (max-width: #{$breakpoint-desktop - 1px}) {
    @content;
  }
}

@mixin desktop {
  @media (min-width: #{$breakpoint-desktop}) {
    @content;
  }
}
