@import "variable.scss";

.center-section {
  .center-wrap {
    padding-top: 15rem;
    padding-bottom: 12rem;
    height: auto;
    min-height: 80vh;

    .title {
      margin-bottom: 2rem;
    }

    .sub-title {
      font-size: 1.1rem;
      margin-bottom: 5rem;
    }
  }

  .notice-table {
    border-top: 2px solid #333;

    .notice-title {
      font-size: 1.1rem;
      font-weight: 500;
    }

    .notice-list {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 1.25rem 1rem;
      cursor: pointer;
      border-bottom: 1px solid #999;

      &:hover {
        background: #f9f9ff;
      }

      &:last-child {
        border-bottom: 2px solid #333;
      }
    }
  }

  .pagination {
    display: flex;
    width: 100%;
    max-width: 18rem;
    margin: 0 auto;
    margin-top: 3rem;
    justify-content: space-between;
    font-size: 1.1rem;

    span {
      cursor: pointer;
      color: #999;

      &:hover {
        color: #666;
      }

      &.active {
        color: #333;
        font-weight: 500;
      }
    }
  }

  .input-div {
    width: 100%;
    font-size: 1.5rem;
    margin: 2.5rem 0;
    display: flex;
    align-items: flex-start;

    label {
      display: inline-block;
      width: 10rem;
      font-weight: 300;

      &.mt-h {
        margin-top: 0.4rem;
      }

      &.mt {
        margin-top: 1.25rem;
      }
    }

    input {
      width: calc(100% - 10rem);
      border-bottom: 1px solid #333;
      font-size: 1.5rem;
      padding: 0.3rem 0;
    }

    textarea {
      width: calc(100% - 10rem);
      border: 1px solid #333;
      font-size: 1.5rem;
      font-family: "Noto Sans KR", sans-serif;
      padding: 0.5rem;
      margin-top: 0.5rem;
      cursor: auto;
      resize: none;
      line-height: 1.8;
    }
  }

  .send {
    display: block;
    margin: 1rem auto;
    padding: 0.75rem 2.5rem;
    font-size: 1.25rem;
    font-weight: 600;
    background: $sub2;
    color: #fff;
    transition: all 0.3s $easing;

    &:hover {
      background: $pri;
      border-radius: 5rem;
    }
  }

  .lookup-box {
    width: 60%;
    margin: 1rem auto;
    border: 1px solid #333;
    padding: 1.5rem;

    .lookup-text {
      margin-bottom: 2rem;
    }

    .apply-box {
      display: flex;
      margin-top: 3rem;
      margin-bottom: 3rem;
      font-size: 1.25rem;
      align-items: center;

      .selection {
        width: 70%;

        span {
          text-align: center;
          width: calc(50% - 2rem);
          display: inline-block;
          cursor: pointer;
          transition: all 0.5s $easing;
          border-radius: 5rem;
          padding: 0.5rem;
          margin: 0 1rem;

          &:hover {
            background: $sub;
          }

          &.active {
            background: #333;
            color: #fff;
          }
        }
      }

      input {
        width: 65%;
        padding: 0.25rem 0;
        margin-left: 2.5%;
        font-size: 1.25rem;
      }
    }

    .label {
      width: 30%;
      text-align: center;
      font-weight: 300;
    }
  }
}
